<template>
  <div class="container-fluid py-3 overflow-hide">
    <div class="row">
      <div class="col-3">
        <input
          type="file"
          class="form-control mb-3"
          multiple
          @change="readFile"
        />
        <input
          type="text"
          class="form-control mb-3"
          placeholder="Splitting text with commas"
          @change="keyInCondition"
        />
        <div class="btn btn-primary" @click="doCompare">Do Compare</div>
      </div>
      <div class="col-9">
        <div class="overflow-auto">
          <div v-if="output.length != 0">
            Match List
            <div v-for="(file, index) in output" :key="index">
              {{ file.name }} - {{ file.value }}
            </div>
          </div>

          <div v-else>Not Match</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
// @ is an alias to /src
import { ref } from "vue";
import $ from "jquery";

let keyWord = ref([]);
let filesData = ref([]);
let output = ref([]);

const keyInCondition = (e) => {
  const condition = e?.target?.value;
  if (condition) {
    keyWord.value = condition.split(",");
  }
};

const readFile = (e) => {
  const files = e?.target?.files;

  filesData.value = [];
  for (let i = 0; i < files.length; i++) {
    const file = files[i];
    const reader = new FileReader();

    reader.onload = function (e) {
      const fileContent = e.target.result;

      // const content = { content: JSON.parse(fileContent) };
      const content = { content: fileContent };
      const output = $.extend(files[i], content);

      filesData.value.push(output);
    };

    reader.readAsText(file);
  }
};

const doCompare = () => {
  output.value = [];
  let length_i = filesData.value.length;
  let length_j = keyWord.value.length;

  for (let i = 0; i < length_i; i++) {
    for (let j = 0; j < length_j; j++) {
      if (filesData.value[i].content.includes(keyWord.value[j])) {
        output.value.push({ name: filesData.value[i].name, value: keyWord.value[j] });
      //  break;
      }
    }
  }
};
</script>

